.mdl-mini-footer {
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 0;
    padding-top: 0;
}

.message-input {
    width: 100%;
    height: 30px;
    background-color: aliceblue;
    color: black
}

.chat {
    list-style: none;
    margin: 0;
    padding: 10px;
    overflow: scroll;
    margin-bottom: 30px;
    overflow: hidden;
    overflow-y: scroll;
}

.message {
    display:inline-block;
    clear: both;
    padding: 10px 15px 10px 15px;
    border-radius: 30px;
    margin-bottom: 2px;
    font-family: Helvetica, Arial, sans-serif;
    max-width: 80%;
    overflow-wrap: break-word;
    -webkit-animation: popIn 0.5s;
    animation: popIn 0.5s;
}

.from {
    float: right;
    background: #0084ff;
    color: #fff;
}

.to {
    background: #eee;
    float: left;
}

.footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: rgb(101, 101, 139);
    color: white;
    text-align: center;
    padding: 1px;
}

@-webkit-keyframes popIn {
    from {bottom: 0; opacity: 0;} 
    to {bottom: 30px; opacity: 1;}
}

@keyframes popIn {
    from {bottom: 0; opacity: 0;}
    to {bottom: 30px; opacity: 1;}
}


